.castList {
    list-style: none;
    margin: 0 auto;
    padding: 0;
  
    max-width: 400px;
}
  
.item {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    padding: 10px 20px;
  
    border-radius: 5px;
    background-color: #ffffff;
    box-shadow: -1px 2px 10px 2px rgba(34, 60, 80, 0.2);
}
  
.avatar {
    display: block;
    width: 100%;
    height: auto;
    max-width: 48px;
  
    margin-right: 15px;
    padding: 5px;
    border: 1px solid transparent;
    border-radius: 10px;
  
    background-color: #f5f5f5;
}
  
.name {
    margin: 0;
  
    font-size: 18px;
    font-weight: 500;
    color: #212121;
}